#info {
    width: 100%;
    padding: 100px 0;
    background: #fbf6f2;
    .inner {
        width: 1180px;
        margin: 0 auto 70px;
        .text {
            display: flex;
            justify-content: center;
            align-content: space-between;
            width: 70%;
            margin: 0 auto 100px;
            padding-bottom: 30px;
            border-bottom: 1px solid $baseColor;
            p {
                &:first-of-type {
                    margin-right: 20px;
                    color: $pointColor;
                    font: 26px/1.3 "Italiana";
                    letter-spacing: 3px;
                }
                &:nth-of-type(2) {
                    font-size: 16px;
                    line-height: 2.2;
                }
            }
        }
        .img-wrap {
            display: flex;
            justify-content: center;
            align-content: space-between;
            .img-card {
                padding: 20px;
                width: 100%;
                position: relative;
                .img {
                    width: 90%;
                    margin-right: 20px;
                    height: 275px;
                }
                &:nth-of-type(1) {
                    .img {
                        background: url(/public/img/main7.jpg) no-repeat center;
                        background-size: cover;
                    }
                }
                &:nth-of-type(2) {
                    .img {
                        background: url(/public/img/main3.jpg) no-repeat center;
                        background-size: cover;
                    }
                }
                &:nth-of-type(3) {
                    .img {
                        background: url(/public/img/main4.jpg) no-repeat bottom center;
                        background-size: cover;
                    }
                }
                figcaption {
                    position: absolute;
                    right: 0;
                    bottom: -50px;
                    padding: 30px 50px;
                    background: #f6f2ef;
                    .img-number {
                        font: 40px/1 "Italiana";
                        color: $pointColor;
                        letter-spacing: 3px;
                        margin-bottom: 20px;
                    }
                    .img-title {
                        font-size: 28px;
                        letter-spacing: 2px;
                        text-transform: uppercase;
                    }
                }
            }
        }
    }
}
